input{
  display: flex;
  padding: 20px;
  margin: auto;
  margin-bottom: 10px;
}

.container{
  margin-left: 10px;
}

.col{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  overflow-x: none;
  flex-wrap: wrap;
}

.card{
  display: flex;
  height: 250px;
  width: 300px;
  margin: 10px;
  padding: 10px;
  border: 1px solid black;
}

.info{
  overflow: hidden;
}

.img{
  display: flex;
  align-self: left ;
  padding: 10px;
  max-height: 200px;
}

.name{
  display: flex;
  align-self: right;
  margin-top: 20px;
  font-weight: 800;
  font-size: 20px;
}


.row{
  margin-top: 10px;
}